// Variables

$primary-color:       #EE4C7C;
$secondary-color:     #5D001E;
$tertiary-color:      #E3AFBC;
$quaternary-color:    #9A1750;
$background-color:    #F8FCFA;
$text-color:          #343a40;
$border-radius:       10px;
$btn-radius:          25px;

$small-screen-width: 768px;

$body-font: stolzl, Montserrat, sans-serif;

// Base Styles
body {
  background-color: $background-color;
  color: $text-color;
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: $body-font;
  font-weight: 200;
}

h6, .h6, h5, .h5, h4, .h4, h3, .h3, h2, .h2, h1, .h1 {
  font-weight: 600;
  font-family: serenity, serif;
}

h1, .h1 {
  font-size: 1.5rem;
}

h2, .h2 {
  font-size: 1.3rem;
}

h3, .h3 {
  font-size: 1.2rem;
}

h4, .h4 {
  font-size: 1.1rem;
}

h5, .h5 {
  font-size: 1rem;
}

p {
  font-size: 1rem;
}

a {
  color: $primary-color;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
    color: $primary-color;
  }
}

section {
  padding: 2rem;
}

nav {
  font-weight: 500;
}

footer{
  font-size: calc(0.8rem);
  a {
    color: $text-color;
    &:hover {
    text-decoration: underline;
    color: $text-color;
  }
  }
}

.content {
  overflow: hidden;
}

.primary-color{
  color: $primary-color;
}

.text-color{
  color: $text-color;
}

.bottom{
  position : absolute;
  bottom   : 0;
}

.text-black {
  color: #000000;
}

.text-decoration-hover {
  text-decoration: none;
  &:hover {
    text-decoration: underline;
    color: $primary-color;
  }
}

.text-small {
  font-size: 0.9rem;
}

.highlighted {
  font-weight: 300;
}